.columns {
  display: flex;
  justify-content: flex-start;
  column-gap: 40px;
  align-items: flex-start;
}

.columns-button {
  margin-top: 20px;
  padding: 5px 10px;
  font-size: 16px;
  line-height: 24px;
  color: var(--main-grey);
  border: none;
  box-shadow: 0 0 4px var(--main-black);
  border-radius: 4px;
  background-color: var(--main-green);
}
